import styled, { css } from 'styled-components';

const Header = styled.h1`
  color: ${(p) => p.theme.text};
  font-size: 2.8rem;
  font-weight: 900;

  @media screen and (max-width: 768px) {
    font-size: 1.6rem;
  }

  ${(p) =>
    p.primary &&
    css`
      color: ${(p) => p.theme.accent.primary};
    `}

  ${(p) =>
    p.large &&
    css`
      font-size: 4rem;

      @media screen and (max-width: 768px) {
        font-size: 2rem;
      }
    `}
`;

export default Header;
